/* back-to-top */

#back-to-top{
    cursor: pointer;
    color:#26262283;
    position: fixed;
    bottom:3rem;
    right:3rem;
    font-size:2rem;
    border: 2px solid #26262283;
    padding:0.5rem 1rem;
background-color: transparent;
outline:0;
z-index: 1000;
}

#back-to-top:hover{
    background-color:#fff;
}

@media  screen and (min-width:768px) {
#back-to-top{    
    font-size:3rem;
    padding:0.5rem 1.5rem;
    border: 3px solid #26262283;
}
}

