    display:flex;
    justify-content: center;
}

.form-btn{
    outline: 0;
    border:0;
    background-color: rgba(34,34,34,0.9);;
    white-space: nowrap;
    color:#fff;
    padding:1.5rem 4rem;
    text-transform: uppercase;
    cursor: pointer;
    letter-spacing: 1px;
}

.form-btn:hover{
    background-color: var(--color-accent);
}

.form-control{
    outline:0;
    border:none;
    border-bottom:1px solid #aeaeae;
    letter-spacing: 1px;
    color:#777;
    transition: all 0.2s cubic-bezier(0, 0, 0.58, 1);
    padding: 1rem 0.5rem;
}

.form-control:focus{
    border:1px solid var(--color-secondary);
   
}

.form-control-message{
    border:1px solid #aeaeae;
    transition: all 0.2s cubic-bezier(0, 0, 0.58, 1);
}

.form-control-message:focus{
    border:1px solid var(--color-secondary);
    
}

.form-group{
    margin:1.5rem 0;
}

.form-grid{
    display:grid;
}

@media screen and (min-width:768px){
.form-grid--1x2{
    grid-template-columns: repeat(2,1fr);
    gap: 2rem;
}
.form-btn-wraper{
    justify-content: flex-end;
}
}

/* membership */

.membership__body{
    padding:4rem 2rem;
    margin:0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ccpa_logo__image{
     max-width:250px;
     width:100%;
}

.membership-wrap{
    padding:1rem 2rem;
}
@media screen and (min-width:768px) {

    .membership__body{
    display:flex;
    flex-direction: row;
    justify-content:center;
    }

}